html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.7rem;
  background-color: hsl(0deg, 0%, 100%);
  background-image: radial-gradient(at 57% 9%, hsl(82deg, 0%, 100%) 0px, transparent 50%), radial-gradient(at 33% 100%, hsl(82deg, 0%, 100%) 0px, transparent 50%), radial-gradient(at 100% 100%, hsl(278deg, 100%, 92%) 0px, transparent 50%), radial-gradient(at 0% 0%, hsl(341deg, 100%, 89%) 0px, transparent 50%);
}
body.fijar {
  overflow: hidden;
}

p {
  font-size: 2rem;
  color: #262626;
  line-height: 1.5;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h2 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 3.6rem;
}

h2 {
  font-size: 3.4;
}

h3 {
  font-size: 3rem;
}

img {
  max-width: 100%;
  display: block;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 1.5rem;
}
.header h1 {
  color: #262626;
  text-align: center;
  margin: 0;
}
.header .menu img {
  width: 35px;
  height: 35px;
  border-radius: 2px;
}
.header .menu,
.header #check {
  display: none;
}
.header .check-boton {
  display: block;
}
@media (min-width: 768px) {
  .header .check-boton {
    display: none;
  }
}
.header .menu {
  display: block;
  position: fixed;
  top: 2rem;
  right: 2rem;
  cursor: pointer;
  background-color: hsl(0deg, 0%, 100%);
  border-radius: 0.3rem;
  z-index: 2;
}
.header ul {
  z-index: 1;
  list-style: none;
  padding: 5rem 0;
  display: block;
  position: fixed;
  top: 0;
  left: -100%;
  background-color: rgb(255, 255, 255);
  width: 100%;
  height: 100vh;
  right: 0;
}
@media (min-width: 768px) {
  .header ul {
    position: relative;
    display: flex;
    top: 0;
    left: 0;
    background: none;
    height: auto;
    padding-top: 0rem;
  }
}
.header ul li {
  padding: 8rem;
  display: flex;
  justify-content: center;
  margin: 0;
}
@media (min-width: 768px) {
  .header ul li {
    padding: 0;
  }
}
.header #check:checked ~ ul {
  left: 0;
  transition: all 0.25s;
}

.navegacion-principal {
  margin: 0;
}
@media (min-width: 768px) {
  .navegacion-principal {
    display: flex;
    gap: 2rem;
  }
}
.navegacion-principal a {
  display: block;
  text-align: center;
  color: #e6acb6;
  font-size: 2.3rem;
  position: relative;
  font-weight: bold;
  margin: 0;
}
@media (min-width: 768px) {
  .navegacion-principal a {
    color: #262626;
  }
}
.navegacion-principal a:after {
  content: "";
  position: absolute;
  width: 0;
  height: 3px;
  display: block;
  margin-top: 5px;
  right: 0;
  background: #262626;
  transition: width 0.2s ease;
  -webkit-transition: width 0.2s ease;
}
.navegacion-principal a:hover:after {
  width: 100%;
  left: 0;
  background: #262626;
}

.inicio {
  height: 90vh;
}

.titulo {
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
}
.titulo h2 {
  font-size: 6rem;
  margin: 0;
  color: #262626;
  margin-bottom: 1rem;
}
.titulo p {
  font-size: 2.6rem;
  font-weight: bold;
  margin: 0;
  color: white;
  background-color: #262626;
  display: inline-block;
  padding: 1rem 4rem;
  border-radius: 10rem;
}

.festival {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .festival {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 10rem 5rem;
  }
}

.info-festival {
  margin-top: 3rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media (min-width: 768px) {
  .info-festival {
    margin-top: 0;
  }
}
.info-festival h2 {
  font-family: "Abril Fatface", cursive;
  font-weight: 600;
  margin: 0;
  font-size: 3.5rem;
}
.info-festival .fecha {
  color: #e6acb6;
  font-weight: bold;
  font-size: 2rem;
  margin: 2rem 0rem;
}
.info-festival p {
  font-size: 1.9rem;
  margin: 0;
  margin-bottom: 10rem;
}
@media (min-width: 768px) {
  .info-festival p {
    margin-bottom: 0;
  }
}

.fechas {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .fechas {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 5rem 0;
    gap: 4rem;
  }
}

.lineup {
  text-align: center;
  padding: 0rem 0rem;
}
@media (min-width: 768px) {
  .lineup {
    padding: 7rem 0rem;
  }
}
.lineup h3,
.lineup p {
  text-align: center;
  color: white;
}
.lineup h3 {
  font-size: 4rem;
  border-top: 1rem solid #e6acb6;
  display: inline-block;
  padding-top: 7rem;
  color: #363636;
}
.lineup .nombre-grupo {
  font-weight: bold;
}
.lineup .dia {
  text-transform: uppercase;
  margin-bottom: 1rem;
  color: #e6acb6;
  font-weight: bold;
}
.lineup .horario {
  margin-bottom: 6rem;
}
.lineup ul li {
  list-style: none;
  font-weight: bold;
  font-size: 1.9rem;
  padding: 1.5rem;
}
.lineup ul {
  padding: 0;
}
.lineup li:nth-child(odd) {
  background-color: rgba(190, 161, 161, 0.151);
}
.lineup span {
  font-weight: normal;
}
.lineup .horarios-contenedor {
  background-color: #363636;
  padding: 1rem 0rem;
  margin-bottom: 2rem;
  margin: 3rem;
  color: white;
  box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
}
@media (min-width: 768px) {
  .lineup .horarios-contenedor {
    margin: 0;
  }
}

.galeria {
  padding: 5rem 0;
}
.galeria h3 {
  text-align: center;
}

.galeria-img {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem 2rem;
}
@media (min-width: 480px) {
  .galeria-img {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem 2rem;
  }
}
@media (min-width: 768px) {
  .galeria-img {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem 2rem;
  }
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.8);
}
.overlay picture {
  display: block;
  max-width: 90rem;
}

.tickets {
  padding: 5rem 0;
  position: relative;
  z-index: -1;
}
.tickets h3 {
  text-align: center;
}

.ticket-general,
.ticket-vip {
  text-align: center;
  font-size: 2rem;
  letter-spacing: 2px;
  font-weight: 600;
}

.ticket {
  margin: 2rem auto;
  width: 100%;
  display: flex;
  justify-content: space-between;
  background: white;
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
}
@media (min-width: 768px) {
  .ticket {
    width: 80%;
  }
}

.album, .der {
  width: 15rem;
  background-size: contain;
  opacity: 0.85;
}
@media (min-width: 768px) {
  .album, .der {
    width: 25rem;
  }
}

.admitir {
  position: absolute;
  color: #262626;
  font-weight: bold;
  height: 120px;
  font-size: 0.7rem;
  padding: 0 10px;
  letter-spacing: 0.15em;
  display: flex;
  text-align: center;
  justify-content: space-around;
  writing-mode: vertical-rl;
  transform: rotate(-180deg);
  margin: 0;
}
@media (min-width: 480px) {
  .admitir {
    height: 140px;
    font-size: 1rem;
  }
}
@media (min-width: 768px) {
  .admitir {
    height: 250px;
  }
}

.der {
  border-left: 1px dashed #404040;
}

.ticket-info {
  padding: 1rem;
}
@media (min-width: 768px) {
  .ticket-info {
    padding: 2.5rem;
  }
}
.ticket-info h2 {
  margin-bottom: 1rem;
  font-size: 1.5rem;
  text-align: center;
  margin-top: 1rem;
}
@media (min-width: 768px) {
  .ticket-info h2 {
    font-size: 3rem;
  }
}
.ticket-info .precio {
  margin: 0;
  background-color: rgb(255, 6, 6);
  color: white;
  font-weight: 600;
  display: inline-block;
  padding-inline: 1rem;
  font-size: 1.5rem;
}
@media (min-width: 768px) {
  .ticket-info .precio {
    font-size: 2rem;
  }
}
.ticket-info .localidad {
  font-weight: 600;
  font-size: 1rem;
  margin: 1rem 0 0 0;
}

.date {
  border-top: 1px solid gray;
  border-bottom: 1px solid gray;
  padding: 5px 0;
  font-weight: 700;
  display: flex;
  align-items: center;
  justify-content: space-around;
  font-size: 1rem;
  gap: 1rem;
  margin: 0;
}

.date span:nth-child(2) {
  color: #e6acb6;
}

.img-qr img {
  padding: 2.5rem;
}

.vip .precio {
  background-color: #fbff00;
  color: #262626;
}

.web-footer {
  background-color: #262626;
  padding: 3rem;
  margin-top: 7rem;
}
.web-footer p {
  margin: 0;
  text-align: center;
  font-size: 1.5rem;
  color: white;
}